<script>
	import Tabbar from '../../my_common_components/Tabbar.vue'
	import '../../common/font/iconfont.css';
	import fetch from '../../utils/fetch.js'
	export default {
		components: {
			Tabbar
		},
		created() {
			if (this.isRole) {
				this.getInquiryList()
			}
		},
		data() {
			return {
				//上方全局搜索
				keywords: "",
				//默认checkbox选中颜色
				activeColor: "#D0021B",
				//全选
				allChecked: false,
				//总的数据
				list: [],
				//竞价数组
				biddingList: [],
				//询盘数组
				Inquiry: [],
				//实单数组
				realOrderList: []

			}
		},
		methods: {
			// 全选
			checkboxAllChange() {
				this.allChecked ? this.list.map(val => {
					val.checked = true;
				}) : this.list.map(val => {
					val.checked = false;
				})
			},

			//单选
			checkboxOneChange(e) {
				this.allChecked = (this.list.length === this.list.filter(val => val.checked).length)
			},

			//点击详情跳转
			toBidding(obj) {
				obj.status='quotedPrice'
				this.$store.dispatch('checkOne', obj)
				uni.navigateTo({
					url: '../bidding/index'
				})
			},
			//询价单列表
			async getInquiryList() {
				let res = await fetch(this.api.v2.inquiryList, {
					method: "get",
					data: {
						accessToken: uni.getStorageSync('accessToken'),
						keywords: this.keywords
					}
				})
				if (res.data.code === '0') {
					this.list = []
					this.biddingList = []
					this.Inquiry = []
					this.realOrderList = []
					this.list = res.data.data.list
					if (this.list && this.list.length > 0) {
						this.list.forEach((item, index) => {
							item.checked = false
							item.down = false
							item.name = index
							item.id = index
							item.currency = 'CNY'
						})
					}
				}
			},

			//点击搜索按钮
			checkedAll() {
				this.getInquiryList()
			}
		},

		computed: {
			//判断哪个角色权限
			isRole() {
				return uni.getStorageSync('roleId') === 1 ? true : false
			},
			showTag() {
				return function(item) {
					return item.biddingMode === '是' ? '竞价' : item.inquiryType === "询盘询价" ? "询盘" : "实单"
				}

			}
		}
	}
</script>

